.c-match-viewer {
  height: 100%;
  max-width: 100vw;
  display: grid;
  grid-template-rows: [sets-start] minmax(0, 1fr) [sets-end input-start] auto [input-end];
  grid-template-columns: [sets-start input-start] 100% [sets-end input-end]; }
  .c-match-viewer__sets {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    grid-area: sets; }
  .c-match-viewer .c-score-column {
    -ms-flex-negative: 0;
        flex-shrink: 0; }
  .c-match-viewer__add-game {
    grid-area: input;
    padding: 20px; }

@media (max-width: 900px) {
  .c-match-viewer__sets {
    min-width: 900px; } }

@media (min-width: 1000px) {
  .c-match-viewer {
    grid-template-columns: [sets-start] 1fr [sets-end input-start] auto [input-end];
    grid-template-rows: [sets-start input-start] 100% [sets-end input-end]; } }



